<template>
  <v-app>
    <v-app-bar
      app
      color="#fe6792"
      dark
      dense
    >
      <v-app-bar-nav-icon class="d-lg-none" @click="drawer = !drawer"></v-app-bar-nav-icon>
      <div class="hidden-md-and-down" style="height: 100%;">
        <v-btn
          v-for="(election, index) in elections"
          :key="index"
          @click="select(election)"
          text
          tile
          style="height: 100%;"
        >
          <span class="mr-2">{{ election.title }}</span>
        </v-btn>
      </div>
    </v-app-bar>

    <v-navigation-drawer
      v-model="drawer"
      absolute
      temporary
      bottom
    >
      <v-list
        dense
      >
        <v-list-item-group>
          <v-list-item v-for="(election, index) in elections" :key="index" @click="select(election)">
            {{ election.title }}
          </v-list-item>
        </v-list-item-group>
      </v-list>
    </v-navigation-drawer>

    <v-main style="background-color: #dcdcdc;">
      <Election :data="selected" />
    </v-main>
  </v-app>
</template>

<script>
import Election from './components/Election'

export default {
  name: 'App',

  components: {
    Election
  },

  data: function () {
    const parties = require('@/assets/parties.json')
    const PATHS = ['22localbig', '22localsmall', '20legislative']
    const elections = []
    for (let election of PATHS) {
      election = require(`@/assets/elections/${election}/index.json`)
      for (let i = 0; i < election.parties.length; i++) {
        election.parties[i] = parties[election.parties[i]]
      }
      elections.push(election)
    }
    return {
      elections: elections,
      selected: elections[0],
      drawer: false
    }
  },

  methods: {
    select: function (election) {
      this.drawer = false
      this.selected = election
    }
  }
}
</script>

<style>
.v-toolbar__content {
  padding-top: 0 !important;
  padding-bottom: 0 !important;
}
</style>
